
.main-clr{color:rgb(208,53,57);}




.first{width:100%;padding:80px 0;color:#242349;letter-spacing:1px;display: flex;flex-direction: column;align-items: center;letter-spacing:1px;}
.bottom{width:90%;max-width:1250px;}
.bottom>.first-box-cover{min-width:100%;
  }
  #animation{ transition-property:all;transition-timing-function:linear;transition-duration:0.5s;}
.bottom>.first-box-cover>.first-box{cursor: pointer;display: flex;justify-content: center;}
.bottom>.first-box-cover>.first-box>div{display: flex;flex-direction: column;align-items: center;width:93%;border-radius:5px;}
.bottom>.first-box-cover>.first-box>div>span:first-child{transition-property:all;transition-timing-function:linear;transition-duration:0.3s;font-size:100px;}
.bottom>.first-box-cover>.first-box>div>span:first-child:hover{transform: translateY(-10px);}

.first-box-circles{display:none;justify-content: center;}
.first-box-circles>span{width:15px;height:15px;border-radius:3px;background-color:black;margin:0 3px;cursor: pointer;}
.first-box-circles>span:first-child{background-color:rgb(208,53,57);}
.first-box-circles>span{transition-property:all;transition-timing-function:linear;transition-duration:1s;}
.first-box-circles>span:hover{transform:scale(1.3,1.3)}
@media screen and (max-width:1000px)
{
   .bottom{flex-wrap: nowrap;overflow:hidden;width:100%;}
   .bottom>.first-box-cover{flex-wrap: nowrap;}
   .first-box-circles{display: flex;}
}
