.main-clr{color:rgb(208,53,57);}
.industry{display: flex;justify-content: center;}
.industry>div{width:93%;display: flex;flex-direction: column;align-items: center;}
.industry>div>div{max-width:100%;min-width: 100%;}
.box-cover{display: flex;justify-content: space-between;flex-wrap: wrap;}
.box-cover>.box,.box-cover>.boxs{border-radius:3px;width:300px;height:auto;display: flex;flex-direction: column;align-items: center;justify-content: center;
               cursor: pointer;}
.box-cover>.box>span:first-child,.box-cover>.boxs>span:first-child{font-size:40px;}
.box-cover>.box>span:last-child,.box-cover>.boxs>span:last-child{display: flex;justify-content: center;width:95%;}
.slider{border-radius:3px;overflow:hidden;}
.slider>.slider-inner{display: flex;
  transition-property:all;transition-timing-function:linear;transition-duration:0.5s;}

.slider>.slider-inner>.slider-box{margin:0;}
.slider>.slider-inner>.slider-box>div:last-child{display: flex;flex-direction:column;justify-content:center;}
.slider>.slider-inner>.slider-box>div:first-child{display: flex;justify-content: center;align-items: center;min-width:50%;max-width:50%;}
/*  */
::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color:white !important;
    opacity: 1 !important; /* Firefox */
  }
  .slider>.slider-inner>.slider-box ul {
    list-style: none;
  }
  
  .slider>.slider-inner>.slider-box ul li::before {
    content: "\2022";
    color: rgb(208,53,57);
    font-weight: bold;
    display: inline-block; 
    width: 1em;
    margin-left: -3em;
  }