.scene {
    width:100%;height:320px;perspective: 600px;
   
  }
  .cards {
    position: relative;width: 100%;height: 100%;cursor: pointer;transform-style: preserve-3d;transform-origin: center right;transition: transform 1s;  
  }
  
  .cards.is_flipped {
    transform: translateX(-100%) rotateY(-180deg);
  }
  
  .card__face {
    position: absolute;width:100%;height: 100%;padding:0 10px;color: white;display: flex;justify-content: center;align-items: center; text-align: center;
  }
  
  .card__face_front {
    background-image: linear-gradient(to right,rgb(208,53,57),rgb(225, 75, 80)); border-radius:3px;
    display: flex;justify-content: center;align-items: center;flex-direction: column;
  }
  
  .card__face_back {
    border-radius:3px;background-image: linear-gradient(to right,rgb(208,53,57),rgb(225, 75, 80));transform: rotateY(180deg);
    display: flex;justify-content: center;align-items: center;flex-direction: column;
  }