.getus{flex-wrap: wrap;}
.getus>div{width:400px;display: flex;flex-direction: column;justify-content: center;align-items: center;border-radius:3px;box-shadow:1px 1px 12px #c5c4c4, -1px -1px 12px #c5c4c4;}
.getus>div>img{width:100px;height:auto;}
@media screen and (max-width:450px) 
{
    .getus>div{width:100%}
}