
.repla-navbar{display: flex;justify-content:center; 
    /* background-image: linear-gradient(to right,rgb(208,53,57),rgb(225, 75, 80));  */
    transition-property:all;transition-timing-function:linear;transition-duration:0.5s;width:100%;position: fixed;z-index: 100;
    }
.nav-shadow{box-shadow:1px 1px 7px black;}
.repla-navbar>.repla-navbar-inner{width:97%;display: flex;justify-content: space-between;flex-wrap: wrap;}
.repla-navbar>.repla-navbar-inner>.repla-navbar-left{width:160px;display: flex;align-items: center;}
.repla-navbar>.repla-navbar-inner>ul{display: flex;list-style-type: none;position: relative;}
.repla-navbar>.repla-navbar-inner>ul>li{padding:0 20px;display: flex;align-items: center;}
.repla-navbar>.repla-navbar-inner>ul>li>a{color:white;text-decoration:none;display: flex;font-weight:500;}
.repla-navbar>.repla-navbar-inner>ul>li>a>span{display: flex;align-items: center;}
.repla-navbar>.repla-navbar-inner>ul>li>a>span:first-child{transform: translateX(-15px);transition-property:all;transition-timing-function:linear;transition-duration:0.5s;visibility: hidden;}
.repla-navbar>.repla-navbar-inner>ul>li>a>span:last-child{margin-left:3px;}
.repla-navbar>.repla-navbar-inner>ul>li>ul{display:none;flex-direction: column;position:absolute;top:70px;list-style-type: none;padding:20px 0;margin:0;background-color:black;
                      color:white;border-radius:3px;transition-property:all;transition-timing-function:linear;transition-duration:0.5s;width:220px;
                    
                      }
.repla-navbar>.repla-navbar-inner>ul>li>ul>li{margin:10px 20px 0 20px;cursor: pointer;display: flex;flex-direction: column;}
.repla-navbar>.repla-navbar-inner>ul>li>ul>li>a{color:white;}
.repla-navbar>.repla-navbar-inner>ul>li>ul>li>span{width:30%;border-bottom:1.5px solid rgb(208,53,57);padding-top:10px;
    transition-property:all;transition-timing-function:linear;transition-duration:0.3s;}
.repla-navbar>.repla-navbar-inner>.repla-navbar-right{display: flex;align-items: center;justify-content:flex-end;width:160px;}
.arrowDown{ transition-property:all;transition-timing-function:linear;transition-duration:0.3s;font-size:12px;}
/* replanavbar small */
.repla-navbar-small{display:none;justify-content:center; padding:20px 0;
   width:100%;position: fixed;top:0;z-index: 100;}
.repla-navbar-small>.repla-navbar-inner{width:97%;display: flex;justify-content: space-between;flex-wrap: wrap;}
.repla-navbar-small>.repla-navbar-inner>.repla-navbar-left{width:160px;display: flex;align-items: center;}
.repla-navbar-small>.repla-navbar-inner>.repla-navbar-right{display: flex;justify-content:flex-end;align-items:center;}
.repla-navbar-small .sp-cover{display: flex;flex-direction: column;cursor: pointer;}
.repla-navbar-small .sp{width:40px;border-bottom:1px solid white;height:0px;margin-bottom:8px;}
.repla-navbar-small .sp:last-child{margin-bottom:0;}
.repla-navbar-small .sp-2{width:30px;}
.repla-navbar-small .sp-3{width:35px;}
@media screen and (max-width:1000px) 
{
    .repla-navbar-small{display: flex;}
    .repla-navbar{transform: translateX(-1000px);align-items: center;}
    .repla-navbar-trans{transform: translateX(0px);}
    .repla-navbar>.repla-navbar-inner{flex-direction: column;align-items: center;}
    .repla-navbar>.repla-navbar-inner>.repla-navbar-left,.repla-navbar>.repla-navbar-inner>.repla-navbar-right{display: none;}
    .repla-navbar>.repla-navbar-inner>ul{flex-direction: column;width:100%;}
    .repla-navbar>.repla-navbar-inner>ul>li{flex-direction: column;padding:20px 0;width:100%;}
}
