.main{width:100%;display: flex;justify-content: center;}
.main>div{width:99%;border-radius:3px;}
.footer{background-color: rgb(230, 227, 224);}
.footer>div{display: flex;justify-content: space-between;flex-wrap: wrap;align-items: center;}
.footer>div>div{display: flex;align-items: center;}
.footer>div>div>span{margin-left:8px;cursor: pointer;font-size:18px;margin-top:0;}
@media screen and (max-width:850px)
{
    .footer>div{flex-direction: column;justify-content: center;}
}