.analysis_counter{background-size: cover;background-position: center;display: flex;justify-content: center;
    background: linear-gradient(to right,rgb(208,53,57),rgb(225, 75, 80))
}
.analysis_counter>div{width:97%;display: flex;justify-content: space-around;flex-wrap: wrap;}
.analysis_counter>div>div{width:150px;display: flex;flex-direction: column;align-items: center;color: white;}
.analysis_counter>div>div>span:nth-child(2),.analysis_counter>div>div>span:nth-child(1){font-size:45px;font-weight:bold;color: white;}
.analysis_counter>div>div>span:last-child{font-size: 18px;transform: translateY(-7px);}