
.main-clr{color:rgb(208,52,63);}
.section{display:flex;justify-content: center;}
.section>.section-inner{width:97%;}



.first{width:100%;padding:80px 0;color:#242349;letter-spacing:1px;display: flex;flex-direction: column;align-items: center;letter-spacing:1px;}
.bottom{width:100%;max-width:1250px;flex-wrap: nowrap;overflow:hidden;}
.bottom>.first-box-cover{min-width:100%;flex-wrap: nowrap;
  }
  #animation{ transition-property:all;transition-timing-function:linear;transition-duration:0.5s;}
.bottom>.first-box-cover>.first-box{cursor: pointer;display: flex;justify-content: center;}
.bottom>.first-box-cover>.first-box>div{display: flex;flex-direction: column;align-items: center;width:93%;border-radius:5px;}
.bottom>.first-box-cover>.first-box>div>h6{max-width:95%;font-weight: bold;}
.bottom>.first-box-cover>.first-box>div>img{transition-property:all;transition-timing-function:linear;transition-duration:0.3s;}
.bottom>.first-box-cover>.first-box>div>img:hover{transform: translateY(-10px);}
.bottom p{width:80%;}
.bottom img{width:140px;height:auto;}
.first-box-circle{display: flex;justify-content: center;display:flex;}
.first-box-circle>span{width:12px;height:12px;background-color:#ffffff;border-radius: 50%;background-color:rgb(231,31,31);margin:0 3px;cursor: pointer;}
.first-box-circle>span:first-child{background-color:rgb(208,52,63);}
.first-box-circle>span{transition-property:all;transition-timing-function:linear;transition-duration:1s;}
